import React, { useState, useMemo, useEffect } from 'react'
import TextField from '@mui/material/TextField'
import Autocomplete from '@mui/material/Autocomplete'
import CircularProgress from '@mui/material/CircularProgress'
import { Group } from 'state/group/actions'
import { useGroups } from 'state/group/hooks'

// function sleep(delay = 0) {
//   return new Promise((resolve) => {
//     setTimeout(resolve, delay)
//   });
// }

export default function GroupSelect({ value, onChange }: { value?: Group; onChange?: (value: Group | null) => void }) {
  const [open, setOpen] = useState(false)
  const [options, setOptions] = useState<readonly Group[]>([])
  const loading = open && options.length === 0
  
  const groups = useGroups()
  const sortedGroups = useMemo(() => {
    return groups ? [...groups].sort((a, b) => a.name.localeCompare(b.name)) : []
  }, [groups])

  useEffect(() => {
    let active = true;

    if (!loading) {
      return undefined;
    }

    (async () => {
      // await sleep(1e3)

      if (active) {
        setOptions([...sortedGroups])
      }
    })()

    return () => {
      active = false;
    }
  }, [loading, sortedGroups])

  useEffect(() => {
    if (!open) {
      setOptions([])
    }
  }, [open])

  const handleChange = (event: React.SyntheticEvent<Element, Event>, value: Group | null) => {
    onChange && onChange(value)
  }

  return (
    <Autocomplete
      disablePortal
      id="group-select"
      sx={{
        width: '100%',
        '.MuiOutlinedInput-root': {
            padding: 0,
            fontSize: '0.875rem',
        },
        '.MuiOutlinedInput-root .MuiAutocomplete-input': {
            padding: '0.375rem 0.75rem',
        },
        '& .MuiOutlinedInput-notchedOutline': {
            '& > legend': {
            float: 'left !important',
            }
        },
      }}
      open={open}
      onOpen={() => setOpen(true)}
      onClose={() => setOpen(false)}
      onChange={handleChange}
      isOptionEqualToValue={(option, value) => option.id === value.id}
      getOptionLabel={(option) => option.name}
      options={options}
      loading={loading}
      defaultValue={value}
      renderInput={(params) => (
        <TextField
          {...params}
          placeholder={'选择分组'}
          InputProps={{
            ...params.InputProps,
            endAdornment: (
              <React.Fragment>
                {loading ? <CircularProgress color="inherit" size={20} /> : null}
                {params.InputProps.endAdornment}
              </React.Fragment>
            ),
          }}
        />
      )}
    />
  )
}
