import React, { useMemo, useState } from 'react'
import { useTranslation } from 'react-i18next'
import { useQuery, useMutation } from '@apollo/client'
import { PageWrapper } from 'theme/components'
import { Title } from '../styled'
import {
    styled,
    alpha,
    Grid,
    Box,
    Typography,
    Breadcrumbs,
    Menu,
    MenuItem,
    Button,
    Card,
    CardContent,
    Paper,
    TableContainer,
    Table,
    TableHead,
    TableBody,
    TableRow,
    TableCell,
    Link,
    Toolbar,
    IconButton,
    TablePagination,
    Drawer,
    Checkbox,
    Tooltip
 } from '@mui/material'
import LoadingButton from '@mui/lab/LoadingButton'
import { Plus, Chrome, Edit, MapPin, MoreVertical, Trash2 } from 'react-feather'
import { MenuProps } from '@mui/material/Menu'
import Fingerprint from '@mui/icons-material/Fingerprint'
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown'
import FirstPageIcon from '@mui/icons-material/FirstPage'
import KeyboardArrowLeft from '@mui/icons-material/KeyboardArrowLeft'
import KeyboardArrowRight from '@mui/icons-material/KeyboardArrowRight'
import LastPageIcon from '@mui/icons-material/LastPage'
import { useTheme } from '@mui/material/styles'

import TaskCard from './components/TaskCard'

import { useAccountState } from 'state/account/hooks'
import { Task } from 'state/task/actions'
import { useActionHandlers } from 'state/task/hooks'
import { formattedDate } from 'utils'

import { GET_TASKS, START_TASK } from 'constants/Queries'

const StyledCell = styled(TableCell)(() => ({
  fontFamily: '"DM Sans", sans-serif',
  fontWeight: 500,
  fontSize: '0.75rem',
}))

const StyledMenu = styled((props: MenuProps) => (
  <Menu
    elevation={0}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'right',
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'right',
    }}
    {...props}
  />
))(({ theme }) => ({
  '& .MuiPaper-root': {
    borderRadius: 6,
    marginTop: theme.spacing(1),
    minWidth: 180,
    color:
      theme.palette.mode === 'light' ? 'rgb(55, 65, 81)' : theme.palette.grey[300],
    boxShadow:
      'rgb(255, 255, 255) 0px 0px 0px 0px, rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px',
    '& .MuiMenu-list': {
      padding: '4px 0',
    },
    '& .MuiMenuItem-root': {
      fontSize: '0.85rem',

      '& .MuiSvgIcon-root': {
        fontSize: 18,
        color: theme.palette.text.secondary,
        marginRight: theme.spacing(1.5),
      },

      '&:active': {
        backgroundColor: alpha(
          theme.palette.primary.main,
          theme.palette.action.selectedOpacity,
        ),
      },

      'svg': {
        marginRight: '0.75rem',
      }
    },
  },
}));

interface TablePaginationActionsProps {
  count: number;
  page: number;
  rowsPerPage: number;
  onPageChange: (
    event: React.MouseEvent<HTMLButtonElement>,
    newPage: number,
  ) => void;
}

function TablePaginationActions(props: TablePaginationActionsProps) {
  const theme = useTheme();
  const { count, page, rowsPerPage, onPageChange } = props;
  const pageCount = rowsPerPage === -1 ? 1 : Math.ceil(count / rowsPerPage)

  const handleFirstPageButtonClick = (
    event: React.MouseEvent<HTMLButtonElement>,
  ) => {
    onPageChange(event, 0);
  };

  const handleBackButtonClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    onPageChange(event, page - 1);
  };

  const handleNextButtonClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    onPageChange(event, page + 1);
  };

  const handleLastPageButtonClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    onPageChange(event, Math.max(0, Math.ceil(count / rowsPerPage) - 1));
  };

  return (
    <Box sx={{ flexShrink: 0, ml: 2.5 }}>
      <IconButton
        onClick={handleFirstPageButtonClick}
        disabled={page === 0}
        aria-label="first page"
      >
        {theme.direction === 'rtl' ? <LastPageIcon /> : <FirstPageIcon />}
      </IconButton>
      <IconButton
        onClick={handleBackButtonClick}
        disabled={page === 0}
        aria-label="previous page"
      >
        {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
      </IconButton>
      <Typography variant='body2' sx={{ display: 'inline-block' }}>{page + 1} / {pageCount}</Typography>
      <IconButton
        onClick={handleNextButtonClick}
        disabled={page >= Math.ceil(count / rowsPerPage) - 1}
        aria-label="next page"
      >
        {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
      </IconButton>
      <IconButton
        onClick={handleLastPageButtonClick}
        disabled={page >= Math.ceil(count / rowsPerPage) - 1}
        aria-label="last page"
      >
        {theme.direction === 'rtl' ? <FirstPageIcon /> : <LastPageIcon />}
      </IconButton>
    </Box>
  )
}

interface State {
  open: boolean
  value?: Task
  fetching: boolean
  page: number
  pageSize: number
  selectedItems: { [key: string]: Task }
  selectedAll: boolean
}

export default function Account() {
  const { t } = useTranslation()
  const { data } = useQuery(GET_TASKS, { pollInterval: 3 * 1000 })
  const [startTask, { loading }] = useMutation(START_TASK, {
    refetchQueries: [
      {query: GET_TASKS},
      'GetTasks',
    ],
  })

  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const openEl = Boolean(anchorEl);

  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(5);

  // // Avoid a layout jump when reaching the last page with empty rows.
  // const emptyRows =
  //   page > 0 ? Math.max(0, (1 + page) * rowsPerPage - rows.length) : 0;

  const handleChangePage = (
    event: React.MouseEvent<HTMLButtonElement> | null,
    newPage: number,
  ) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (
    event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>,
  ) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const [state, setState] = useState<State>({ 
    open: false,
    value: undefined,
    fetching: false,
    page: 0,
    pageSize: 20,
    selectedItems: {},
    selectedAll: false,
  })

  const emptySelected = Object.keys(state.selectedItems).length === 0

  const toggleTask = (open: boolean) => (event: React.KeyboardEvent | React.MouseEvent) => {
    if (
      event.type === 'keydown' &&
      ((event as React.KeyboardEvent).key === 'Tab' ||
        (event as React.KeyboardEvent).key === 'Shift')
    ) {
      return;
    }

    setState({ ...state, open })
  }

  const { account } = useAccountState()
  const { onAdd, onRemove } = useActionHandlers(account)

  const sortedTasks = useMemo(() => {
    return data ? [...data.tasks].sort((a, b) => b.createdTime - b.createdTime) : []
  }, [data])

  const handleClearAll = (event: React.MouseEvent<HTMLElement>) => {
    Object.values(state.selectedItems).forEach(group => onRemove(group))
    setState({ ...state, selectedItems: {}, selectedAll: false })
  }

  const handleSelected = (task: Task) => (event: React.ChangeEvent<HTMLInputElement>) => {
    const { selectedItems } = state
    if (event.target.checked && !selectedItems[task.id]) {
      selectedItems[task.id] = task
    }
    if (!event.target.checked && selectedItems[task.id]) {
      delete selectedItems[task.id]
    }
    
    setState({ 
      ...state, 
      selectedItems,
      selectedAll: sortedTasks.length === Object.keys(selectedItems).length,
    })
  }

  const handleSelectedAll = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.checked) {
      const selectedItems = sortedTasks.reduce((map: { [key: string]: Task }, task) => {
        map[task.id] = task
        return map
      }, {})
      setState({ ...state, selectedItems, selectedAll: true })
    } else {
       setState({ ...state, selectedItems: {}, selectedAll: false })
    }
  }

  const handleClick = (task: Task | undefined) => (event: React.MouseEvent<HTMLElement>) => {
    setState({ ...state, value: task })
    setAnchorEl(event.currentTarget)
  }

  const handleClose = () => {
    setAnchorEl(null)
  }

  const handleChange = (task: Task | undefined) => {
    setState({ ...state, open: false })
    task && onAdd({ ...task, id: `${sortedTasks.length + 1}` })
  }

  const handleEdit = () => {
    setAnchorEl(null)
    state.value && setState({ ...state, open: true })
  }

  const handleRemove = () => {
    setAnchorEl(null)
    state.value && onRemove(state.value)
  }

  const handlePlay = (tasks: Task[]) => (event: React.MouseEvent<HTMLElement>) => {
    console.log(tasks)
    tasks.forEach(task => {
       startTask({ variables: { taskID: task.id } })
    })
  }

  return (
    <PageWrapper>
        <Grid container sx={{ p: 2 }}>
          <Grid item xs={12}>
            <Breadcrumbs aria-label="breadcrumb">
              <Link underline="hover" color="inherit" href="#/account">账号</Link>
              <Typography color="text.primary">任务</Typography>
            </Breadcrumbs>
            <Title variant='h1'>{t('任务')}</Title>
          </Grid>
        </Grid>
        <Toolbar sx={{ padding: '0 1rem !important', '& button': { m: 0.5 }, '& button:first-of-type': { ml: 0 } }}>
          <Button disableElevation variant="contained" size="small" color="secondary" startIcon={<Plus />} onClick={toggleTask(true)} >导入</Button>
          <Button
            disableElevation
            variant="outlined" 
            size="small" 
            color="info" 
            startIcon={<Chrome />}
            disabled={emptySelected}
            onClick={handlePlay(Object.values(state.selectedItems))}
          >
            打开
          </Button>
          <Button
            disableElevation
            variant="outlined" 
            size="small" 
            color="info" 
            startIcon={<Trash2 />}
            disabled={emptySelected}
            onClick={handleClearAll}
          >
            删除
          </Button>
          <Button
            disableElevation
            id="more-button"
            aria-controls={openEl ? 'more-menu' : undefined}
            aria-haspopup="true"
            aria-expanded={openEl ? 'true' : undefined}
            variant="outlined"
            size="small"
            color="info"
            disabled={emptySelected}
            onClick={handleClick(undefined)}
            endIcon={<KeyboardArrowDownIcon />}
          >
            更多
          </Button>
          <StyledMenu
            id="more-menu"
            MenuListProps={{
              'aria-labelledby': 'more-button',
            }}
            anchorEl={anchorEl}
            open={openEl}
            onClose={handleClose}
          >
            <MenuItem onClick={handleClose} disableRipple>
              <Edit size={18} />
              修改任务
            </MenuItem>
            <MenuItem onClick={handleClose} disableRipple>
              <Fingerprint />
              修改代理
            </MenuItem>
            <MenuItem onClick={handleClose} disableRipple>
              <MapPin size={18} />
              更换国家/地区
            </MenuItem>
          </StyledMenu>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }} />
          <TablePagination
            component="div"
            rowsPerPageOptions={[5, 10, 25, { label: '全部', value: -1 }]}
            count={105}
            rowsPerPage={rowsPerPage}
            page={page}
            SelectProps={{
              inputProps: {
                'aria-label': 'rows per page',
              },
              native: true,
            }}
            labelRowsPerPage="总数:"
            labelDisplayedRows={() => ""}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
            ActionsComponent={TablePaginationActions}
            sx={{ '.MuiInputBase-root': { mr: 0 }, display: 'none' }}
          />
        </Toolbar>
        <Card sx={{
            backgroundColor: 'rgb(255, 255, 255)',
            color: 'rgba(0, 0, 0, 0.87)',
            transition: 'box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
            overflow: 'hidden',
            borderRadius: '20px',
            margin: '1rem',
            boxShadow: 'rgb(90 114 123 / 11%) 0px 7px 30px 0px'
        }}>
          <CardContent sx={{ padding: '1rem 0' }}>
            <TableContainer component={Paper} sx={{ boxShadow: 'none' }}>
              <Table aria-label="simple table">
              <TableHead>
                  <TableRow sx={{
                    "& > .MuiTableCell-head": {
                      fontFamily: '"DM Sans", sans-serif',
                      letterSpacing: '0',
                    }
                  }}>
                    <TableCell sx={{ width: '80px', textAlign: 'center' }}>
                      <Checkbox size="small" checked={state.selectedAll} onChange={handleSelectedAll} />
                    </TableCell>
                    <TableCell sx={{ width: '80px', textAlign: 'center' }}>{'序号'}</TableCell>
                    <TableCell sx={{ width: '100px' }}>{'分组'}</TableCell>
                    <TableCell>{'名称/平台'}</TableCell>
                    <TableCell>{'国家'}</TableCell>
                    <TableCell sx={{ width: '80px', textAlign: 'center' }}>{'打开'}</TableCell>
                    <TableCell>{'备注'}</TableCell>
                    <TableCell>{'IP'}</TableCell>
                    <TableCell sx={{ width: '180px' }}>{'导入时间'}</TableCell>
                    <TableCell sx={{ width: '80px', textAlign: 'right' }}>{''}</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
              {sortedTasks.map((task, row) => (
                <TableRow
                  key={task.id}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                  >
                  <StyledCell sx={{ textAlign: 'center' }}>
                    <Checkbox size="small" checked={!!state.selectedItems[task.id]} onChange={handleSelected(task)} />
                  </StyledCell>
                  <StyledCell sx={{ textAlign: 'center' }} scope="row">
                      {row + 1}
                  </StyledCell>
                  <StyledCell scope="row">
                      {task.group}
                  </StyledCell>
                  <StyledCell scope="row">
                      <Typography variant='subtitle2'>{task.name}</Typography>
                      <Typography variant="caption">{task.platform}</Typography>
                  </StyledCell>
                  <StyledCell>
                      {task.country ? `${task.country.name} (${task.country.code})` : '--'}
                  </StyledCell>
                  <StyledCell scope="row" sx={{ textAlign: 'center' }}>
                    {task.status === 'IN_PROGRESS' ? '进行中' : (
                      <Tooltip title="打开任务" arrow>
                        <LoadingButton 
                          disableElevation 
                          variant="contained" 
                          size="small" 
                          color="secondary" 
                          startIcon={<Chrome size={20} />}
                          sx={{
                            borderRadius: '50%',
                            width: '36px',
                            height: '36px',
                            minWidth: '36px',
                            '>span': {
                              margin: 0,
                            }
                          }}
                          loading={loading}
                          onClick={handlePlay([task])}
                        />
                      </Tooltip>
                    )}
                  </StyledCell>
                  <StyledCell scope="row">
                      {task.remark}
                  </StyledCell>
                  <StyledCell scope="row">
                    <Typography variant='subtitle2'>{task.proxy.host}</Typography>
                  </StyledCell>
                  <StyledCell>
                      {task.createdTime ? formattedDate(new Date(task.createdTime * 1000)) : '--'}
                  </StyledCell>
                  <StyledCell>
                    <IconButton aria-label="more-actions" onClick={handleClick(task)}>
                      <MoreVertical size={20} />
                    </IconButton>
                    <StyledMenu
                      id="more-action-menu"
                      MenuListProps={{
                        'aria-labelledby': 'more-action-button',
                      }}
                      anchorEl={anchorEl}
                      open={openEl}
                      onClose={handleClose}
                    >
                      <MenuItem onClick={handleEdit} disableRipple>
                        <Edit size={18} />
                        修改任务
                      </MenuItem>
                      <MenuItem onClick={handleClose} disableRipple>
                        <Fingerprint />
                        修改代理
                      </MenuItem>
                      <MenuItem onClick={handleClose} disableRipple>
                        <MapPin size={18} />
                        更换国家/地区
                      </MenuItem>
                      <MenuItem onClick={handleRemove} disableRipple>
                        <Trash2 size={18} />
                        删除
                      </MenuItem>
                    </StyledMenu>
                  </StyledCell>
                </TableRow>
              ))}
              </TableBody>
          </Table>
          </TableContainer>
          </CardContent>
        </Card>
         <Drawer
            anchor={'right'}
            sx={{ '.MuiDrawer-paper': { borderRadius: '20px 0 0 20px' }}}
            open={state.open}
            onClose={toggleTask(false)}
          >
            <TaskCard onChange={handleChange} onClose={toggleTask(false)} />
          </Drawer>
    </PageWrapper>
  )
}