import React, { useState, useMemo } from 'react'
import { useTranslation } from 'react-i18next'
import { useQuery } from '@apollo/client' 
import { 
  Grid, 
  Typography, 
  Breadcrumbs, 
  Link,
  Card,
  CardContent,
  Paper,
  TableContainer,
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  Checkbox,
  Button,
  IconButton,
  MenuItem,
} from '@mui/material'
import { Plus, Edit, Trash2, MoreVertical } from 'react-feather'
import { PageWrapper } from 'theme/components'
import { Title, StyledMenu } from '../styled'
import CreditCardModal from './components/ContactModal'

import { formattedDate } from 'utils'
import { useAccountState } from 'state/account/hooks'
import { CC } from 'state/cc/actions'
import { useActionHandlers } from 'state/cc/hooks'

import { GET_CONTACTS } from 'constants/Queries'

interface State {
  open: boolean
  value?: CC
  fetching: boolean
  page: number
  pageSize: number
  selectedItems: { [key: string]: CC }
  selectedAll: boolean
}

export default function AccountGroup() {
  const { t } = useTranslation()
  const { data } = useQuery(GET_CONTACTS);

  const [state, setState] = useState<State>({
    open: false,
    value: undefined,
    fetching: false,
    page: 0,
    pageSize: 20,
    selectedItems: {},
    selectedAll: false,
  })

  const { account } = useAccountState()
  const { onAdd, onRemove } = useActionHandlers(account)

  const sortedRows = useMemo(() => {
    return data ? [...data.contacts].sort((a, b) => b.createdTime - a.createdTime) : []
  }, [data])


  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);

  const handleClearAll = (event: React.MouseEvent<HTMLElement>) => {
    Object.values(state.selectedItems).forEach(cc => onRemove(cc))
    setState({ ...state, selectedItems: {}, selectedAll: false })
  }

  const handleSelected = (cc: CC) => (event: React.ChangeEvent<HTMLInputElement>) => {
    const { selectedItems } = state
    if (event.target.checked && !selectedItems[cc.id]) {
      selectedItems[cc.id] = cc
    }
    if (!event.target.checked && selectedItems[cc.id]) {
      delete selectedItems[cc.id]
    }
    
    setState({ 
      ...state, 
      selectedItems,
      selectedAll: sortedRows.length === Object.keys(selectedItems).length,
    })
  }

  const handleSelectedAll = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.checked) {
      const selectedItems = sortedRows.reduce((map: { [key: string]: CC }, cc) => {
        map[cc.id] = cc
        return map
      }, {})
      setState({ ...state, selectedItems, selectedAll: true })
    } else {
       setState({ ...state, selectedItems: {}, selectedAll: false })
    }
  }

  const handleClick = (cc: CC) => (event: React.MouseEvent<HTMLElement>) => {
    setState({ ...state, value: cc })
    setAnchorEl(event.currentTarget)
  }

  const handleClose = () => {
    setAnchorEl(null);
  }

  const handleChange = (cc: CC) => {
    console.log(cc)
    onAdd(cc)
  }

  const handleEdit = () => {
    setAnchorEl(null)
    state.value && setState({ ...state, open: true })
  }

  const handleRemove = () => {
    setAnchorEl(null)
    state.value && onRemove(state.value)
  }

  return (
    <PageWrapper>
      <Grid container sx={{ p: 2 }}>
        <Grid item xs={8}>
          <Breadcrumbs aria-label="breadcrumb">
            <Link underline="hover" color="inherit" href="#/account">账号</Link>
            <Typography color="text.primary">联系人</Typography>
          </Breadcrumbs>
          <Title variant='h1'> {t('联系人')}</Title>
        </Grid>
        <Grid item xs={4} sx={{ 
          display: 'flex', 
          justifyContent: 'flex-end', 
          alignItems: 'center',
          gap: '0.5rem',
        }}>
          <Button 
            disableElevation 
            variant="contained" 
            size="large" 
            color="secondary" 
            startIcon={<Plus />}
            sx={{
              borderRadius: '50%',
              width: '48px',
              height: '48px',
              minWidth: '48px',
              '>span': {
                margin: 0,
              }
            }}
            onClick={() => setState({ ...state, open: true, value: undefined })}
          />
          <Button 
            disableElevation 
            variant="contained" 
            size="large" 
            color="secondary" 
            startIcon={<Trash2 />}
            sx={{
              borderRadius: '50%',
              width: '48px',
              height: '48px',
              minWidth: '48px',
              '>span': {
                margin: 0,
              }
            }}
            disabled={Object.keys(state.selectedItems).length === 0}
            onClick={handleClearAll}
          />
        </Grid>
      </Grid>
      <CreditCardModal 
        open={state.open}
        value={state.value}
        onChange={handleChange}
        onDismiss={() => setState({ ...state, open: false })} 
      />
      <Card sx={{
        backgroundColor: 'rgb(255, 255, 255)',
        color: 'rgba(0, 0, 0, 0.87)',
        transition: 'box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
        overflow: 'hidden',
        borderRadius: '20px',
        margin: '1rem',
        boxShadow: 'rgb(90 114 123 / 11%) 0px 7px 30px 0px'
      }}>
      <CardContent sx={{ padding: '1rem 0' }}>
        <TableContainer component={Paper} sx={{ boxShadow: 'none' }}>
          <Table aria-label="simple table">
            <TableHead>
              <TableRow sx={{
                "& > .MuiTableCell-head": {
                  fontFamily: '"DM Sans", sans-serif',
                  letterSpacing: '0',
                }
              }}>
                  <TableCell sx={{ width: '80px', textAlign: 'center' }}>
                    <Checkbox size="small" checked={state.selectedAll} onChange={handleSelectedAll} />
                  </TableCell>
                  <TableCell>{'分组'}</TableCell>
                  <TableCell>{'名称'}</TableCell>
                  <TableCell>{'国家'}</TableCell>
                  <TableCell sx={{ textAlign: 'center' }}>{'数量'}</TableCell>
                  <TableCell sx={{ width: '180px' }}>{'创建时间'}</TableCell>
                  <TableCell sx={{ width: '80px', textAlign: 'right' }}>{''}</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {sortedRows.map((cc) => (
                <TableRow
                  key={cc.id}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                  >
                  <TableCell sx={{ textAlign: 'center' }}>
                    <Checkbox size="small" checked={!!state.selectedItems[cc.id]} onChange={handleSelected(cc)} />
                  </TableCell>
                  <TableCell scope="row">
                      {cc.group}
                  </TableCell>
                   <TableCell scope="row">
                      {cc.name}
                  </TableCell>
                   <TableCell scope="row">
                      {cc.country ? `${cc.country.name} (${cc.country.code})` : '--'}
                  </TableCell>
                   <TableCell scope="row" sx={{ textAlign: 'center' }}>
                      {cc.numbers.length}
                  </TableCell>
                  <TableCell scope="row">
                      {formattedDate(new Date(cc.createdTime * 1000))}
                  </TableCell>
                  <TableCell sx={{ textAlign: 'right' }}>
                    <IconButton aria-label="more-actions" onClick={handleClick(cc)}>
                      <MoreVertical size={20} />
                    </IconButton>
                    <StyledMenu
                      id="more-action-menu"
                      MenuListProps={{
                        'aria-labelledby': 'more-action-button',
                      }}
                      anchorEl={anchorEl}
                      open={open}
                      onClose={handleClose}
                    >
                      <MenuItem onClick={handleEdit} disableRipple>
                        <Edit size={18} />
                        修改
                      </MenuItem>
                      <MenuItem onClick={handleRemove} disableRipple>
                        <Trash2 size={18} />
                        删除
                      </MenuItem>
                    </StyledMenu>
                  </TableCell>
                </TableRow>
              ))}
              </TableBody>
          </Table>
          </TableContainer>
          </CardContent>
        </Card>
    </PageWrapper>
  )
}