import { createReducer } from '@reduxjs/toolkit'
import {
  Task,
  addTask,
  removeTask,
} from './actions'

export interface TaskState {
  [accountId: string]: {
    [id: string]: Task
  }
}

export const initialState: TaskState = {}

export default createReducer(initialState, builder =>
  builder
    .addCase(addTask, (state, { payload: { accountId, task } }) => {
      const tasks = state[accountId] ?? {}
      tasks[task.id] = task
      state[accountId] = tasks
    })
    .addCase(removeTask, (state, { payload: { accountId, task } }) => {
      const tasks = state[accountId] ?? {}
      if (!tasks) return
      delete tasks[task.id]
    })
)
