import React, { useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { useMutation } from '@apollo/client'
import {
  styled,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  IconButton,
  Typography,
  FormControl,
  Alert,
  AlertTitle,
} from '@mui/material'
import LoadingButton from '@mui/lab/LoadingButton'
import { X } from 'react-feather'
import { Label, StyledOutlinedInput } from '../../styled'
import { Group } from 'state/group/actions'

import { GET_GROUPS, CREATE_GROUP } from 'constants/Queries'

const BootstrapDialog = styled(Dialog)(({ theme }) => ({
  
  '& .MuiDialogContent-root': {
    padding: theme.spacing(2),
  },
  '& .MuiDialogActions-root': {
    padding: theme.spacing(1),
  },
}));

export interface DialogTitleProps {
  id: string
  children?: React.ReactNode
  onClose: () => void
}

const BootstrapDialogTitle = (props: DialogTitleProps) => {
  const { children, onClose, ...other } = props;

  return (
    <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
      {children}
      {onClose ? (
        <IconButton
          aria-label="close"
          onClick={onClose}
          sx={{
            position: 'absolute',
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500],
          }}
        >
          <X />
        </IconButton>
      ) : null}
    </DialogTitle>
  )
}

interface State {
  name: string
}

export default function GroupModal({ open, value, onChange, onDismiss }: {
  open: boolean
  value?: Group
  onChange?: (value: Group) => void
  onDismiss?: () => void
}) {
  const { t } = useTranslation()
  const [values, setValues] = useState<State>({ name: '' })

  const [createGroup, { data, loading, error }] = useMutation(CREATE_GROUP, {
    onCompleted: () => {
      onChange && onChange(data.createGroup)
      handleClose()
    },
    refetchQueries: [
      {query: GET_GROUPS},
    ],
  });

  useEffect(() => {
    if (open && value) {
      setValues({ name: value.name })
    } else {
      setValues({ name: '' })
    }
  }, [value, open])

  const handleChange = (prop: keyof State) => (event: React.ChangeEvent<HTMLInputElement>) => {
    setValues({ ...values, [prop]: event.target.value })
  }

  const handleKeyUp = (event: React.KeyboardEvent<HTMLInputElement>) => {
    if (event && event.key === 'Enter') {
      handleOK()
    }
  }

  const handleOK = () => {
    createGroup({ variables: { name: values.name } })
  }

  const handleClose = () => {
    onDismiss && onDismiss()
  }

  return (
      <BootstrapDialog
        aria-labelledby="gruop-modal-title"
        maxWidth={'sm'}
        fullWidth={true}
        open={open}
        onClose={handleClose}
      >
        <BootstrapDialogTitle id="gruop-modal-title" onClose={handleClose}>
          {value ? t('修改分组') : t('创建分组')}
        </BootstrapDialogTitle>
        <DialogContent dividers>
          <Typography component={Label}>{t('名称')}</Typography>
          <FormControl variant="outlined" sx={{ width: '100%' }}>
            <StyledOutlinedInput
              autoFocus
              type="text"
              value={values.name}
              placeholder={t('填写名称')}
              onChange={handleChange('name')}
              onKeyUp={handleKeyUp}
            />
          </FormControl>
        </DialogContent>
        <DialogActions>
          {error && (
            <Alert severity="error">
              <AlertTitle>Error</AlertTitle>
              {error.message}
            </Alert>
          )}
          <LoadingButton 
            autoFocus
            variant="contained" 
            color="secondary"
            loading={loading}
            onClick={handleOK}
          >
            {t('确定')}
          </LoadingButton>
        </DialogActions>
      </BootstrapDialog>
  )
}
