import { createAction } from '@reduxjs/toolkit'
import { Country } from 'state/cc/actions'

export interface Proxy {
  method: string
  type: string
  host: string
  port: string
  username?: string
  password?: string
}

export interface ProxyType { 
  label: string
  value: string 
}

export interface FingerPrint {
	ostype: string              // 操作系统平台 PC|Android
  os: string                  // Win64|Win32|Linux i686|Linux armv7l|MacIntel
  version: string             //浏览器版本[77,78,...,96]
  userAgent: string           // 不填的话会自动生成
  isIpCreateTimeZone: string  // 基于IP生成对应的时区
  timeZone: string            // isIpCreateTimeZone为false的时候，从提供的时区列表中随机一个
  webRTC: string,             // webrtc 0(替换)|1(允许)|2(禁止)
  position: string,           // 地理位置 0(询问)|1(允许)|2(禁止)
  isIpCreatePosition: string  // 基于IP生成对应的地理位置
  lat: string                 // 经度 isIpCreatePosition为false时填写
  lng: string                 // 纬度 isIpCreatePosition为false时填写
  precisionData: string       // 精度米 isIpCreatePosition为false时填写
  isIpCreateLanguage: string  // 基于IP生成对应的国家语言
  languages: string           // isIpCreateLanguage为false，则需要填写语言，从提供的语言列表中选一个值
  resolutionType: string      // 分辨率 0(跟随电脑)|1(自定义)
  resolution: string          // 自定义时分辨率值 1920 x 1080 | 1920 x 1080 | 800 x 600 | ...
  fontType: string            // 字体 0(系统默认) | 1(自定义) | 2(随机匹配)
  font: string                // fontType非系统默认时，可以随机从字体列表中随机取一些，逗号分隔传入
  canvas: string              // 0(随机) | 1(关闭)
  webGL: string               // webGL图像 0(随机) | 1(关闭)
  webGLMeta: string           // webGL元数据 0(自定义) | 1(关闭)
  webGLManufacturer: string   // webGL厂商，从提供的厂商列表中随机选一个
  webGLRender: string         // webGL渲染，从提供的渲染数据list中随机选一个
  audioContext: string        // Audio 0(随机) | 1(关闭)
  mediaDevice: string         // Media 0(随机) | 1(关闭)
  hardwareConcurrency: string // CPU核心数
  deviceMemory: string        //设备内存
  doNotTrack: string          // 0(开启) | 1(关闭)
  colorDepth: string          // 颜色深度 [48,32, 30, 24, 18, 16, ...] 随机一个颜色深度值
}

export interface Task {
  id: string
  name: string
  status?: string
  platform: string
  url: string
  price: string
  remark: string
  proxy: Proxy
  proxyUrl?: string
  browserFingerPrint: FingerPrint,
  group: string,
  country?: Country,
  createdTime?: number
}

export const addTask = createAction<{ accountId: string; task: Task }>('task/add')
export const removeTask = createAction<{ accountId: string; task: Task }>('task/remove')
