import { createReducer } from '@reduxjs/toolkit'
import {
  CC,
  addCC,
  removeCC,
} from './actions'

export interface CCState {
  [accountId: string]: {
    [id: string]: CC
  }
}

export const initialState: CCState = {}

export default createReducer(initialState, builder =>
  builder
    .addCase(addCC, (state, { payload: { accountId, cc } }) => {
      const ccs = state[accountId] ?? {}
      ccs[cc.id] = cc
      state[accountId] = ccs
    })
    .addCase(removeCC, (state, { payload: { accountId, cc } }) => {
      const ccs = state[accountId] ?? {}
      if (!ccs) return
      delete ccs[cc.id]
    })
)
