import { createAction } from '@reduxjs/toolkit'

export interface Country {
  code: string
  name: string
  phone: string
}

export interface CC {
  id: string
  name: string
  group: string
  country?: Country
  numbers: string[]
  createdTime: number
}

export const addCC= createAction<{ accountId: string; cc: CC }>('cc/add')
export const removeCC = createAction<{ accountId: string; cc: CC }>('cc/remove')
