import React, { Suspense, useMemo, useState } from 'react'
import { Route, Switch, Redirect, RouteComponentProps } from 'react-router-dom'
import { styled } from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Header from '../components/Header'
import Sidebar from '../components/Sidebar'
import Pricing from './Pricing'
import Maintenance from './Maintenance'
import Dashboard from './Dashboard'
import Transfer from './Transfer'
import ChangePassword from './ChangePassword'
import Account from './Account'
import AccountGroup from './Account/Group'
import CreditCard from './Account/CreditCard'
import Help from './Help'
import Settings from './Settings'
import { useAccount } from 'hooks'
import { useAccountState } from 'state/account/hooks'

import { ApplicationModal } from '../state/application/actions'
import { useModalOpen } from '../state/application/hooks'

const theme = createTheme({
  palette: {
    primary: {
      main: '#00CEC3',
    },
    secondary: {
      main: '#066A73'
    },
    info: {
      main: 'rgba(0, 0, 0, 0.65)'
    }
  },
});

const drawerWidth = 265

const AppWrapper = styled('div')(() => ({
  display: 'flex',
  minHeight: '100vh',
  overflow: 'hidden',
  width: '100%',
}))

const Main = styled('main', { shouldForwardProp: (prop) => prop !== 'open' })<{
  open?: boolean;
}>(({ theme, open }) => ({
  flexGrow: 1,
  padding: theme.spacing(3),
  transition: theme.transitions.create('margin', {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  marginLeft: `-${drawerWidth}px`,
  ...(open && {
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: '24px',
  }),
}));

const activedRoutes = [
  {
    path: '/dashboard',
    component: Dashboard,
    exact: true,
    strict: true
  },
  {
    path: '/transfer',
    component: Transfer,
    exact: true,
    strict: true
  },
  {
    path: '/change-password',
    component: ChangePassword,
    exact: true,
    strict: true
  },
  {
    path: '/account',
    component: Account,
    exact: true,
    strict: true
  },
  {
    path: '/account/groups',
    component: AccountGroup,
    exact: true,
    strict: true
  },
  {
    path: '/account/credit-card',
    component: CreditCard,
    exact: true,
    strict: true
  },
  {
    path: '/help',
    component: Help,
    exact: true,
    strict: true
  },
]

function AuthRoute({ actived, ...rest }: { actived: boolean }) {
  return actived ? <Route {...rest} /> : <Redirect to={{ pathname: '/' }} />
}

function RedirectPath({
  location,
}: RouteComponentProps) {
  const { account } = useAccountState()

  if (account) {
    return <Redirect to={{ ...location, pathname: '/account' }} />;
  }

  return <Redirect to={{ ...location, pathname: '/' }} />;
}

export default function App() {
  const account = useAccount()
  const open = useModalOpen(ApplicationModal.NAV)
  const routes = useMemo(() => {
    if (account) {
      return activedRoutes.map(route => <AuthRoute key={route.path} {...route} actived={!!account} />)
    }
    return undefined
  }, [account])

  const [maintenance] = useState<boolean>(false)

  return (
    <Suspense fallback={null}>
      <ThemeProvider theme={theme}>
        <AppWrapper>
        {maintenance ? <Maintenance /> : (
          <>
          {account && (
            <>
              <Header />
              <Sidebar />
              <Settings />
            </>
          )}
          <Main open={open}>
            <Switch>
              <Route exact strict path={'/pricing'} component={Pricing} />
              <Route exact strict path={'/maintenance'} component={Maintenance} />
              {routes}
              <Route 
                exact 
                strict 
                path={'/'} 
                render={props => maintenance ? <Maintenance /> : <Account />} 
              />
              <Route component={RedirectPath} />
            </Switch>
          </Main>
          </>
        )}
        </AppWrapper>
      </ThemeProvider>
    </Suspense>
  );
}
