export function useActionHandlers(account?: Account): {
  onAdd: (task: Task) => void
  onRemove: (task: Task) => void
} {
  const dispatch = useDispatch<AppDispatch>();

  const onAdd = useCallback(
    (task: Task) => {
      account && dispatch(addTask({ accountId: account.id, task }))
    },
    [dispatch, account]
  )

  const onRemove = useCallback(
    (task: Task) => {
      account && dispatch(removeTask({ accountId: account.id, task }))
    },
    [dispatch, account]
  )

  return {
    onAdd,
    onRemove,
  };
}

export function useAllTasks(): { [id: string]: Task } {
  const { account } = useAccountState()
  const state =  useSelector<AppState, AppState['task']>(state => state.task)

  return account ? state[account.id] ?? {} : {}
}