export const GET_TASKS = gql`
  query GetTasks {
    tasks {
      id
      name
      status
      platform
      remark
      proxy {
        host
      }
      group,
      country {
        code
        name
      }
      createdTime
    }
  }
`

export const CREATE_TASK = gql`
  mutation CreateTask($name: String!, $status: TaskStatus!, $platform: String, $url: String!, $price: String, $remark: String, $proxy: ProxyInput, $proxyUrl: String, $group: String!, $country: CountryInput) {
    createTask(input: {
      name: $name,
      status: $status,
      platform: $platform,
      url: $url,
      price: $price,
      remark: $remark,
      proxy: $proxy,
      proxyUrl: $proxyUrl,
      browserFingerPrint: {},
      group: $group,
      country: $country,
    }) {
      id
      name
      status
      platform
      remark
      proxy {
        host
      }
      group,
      country {
        code
        name
      }
      createdTime
    }
  }
`

