import React, { useMemo, useState } from 'react'
import { useTranslation } from 'react-i18next'
import { useQuery } from '@apollo/client'
import { 
  Grid, 
  Typography, 
  Breadcrumbs, 
  Link,
  Card,
  CardContent,
  Paper,
  TableContainer,
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  Checkbox,
  Button,
  IconButton,
  MenuItem,
} from '@mui/material'
import { Plus, Edit, Trash2, MoreVertical } from 'react-feather'
import { PageWrapper } from 'theme/components'
import { Title, StyledMenu } from '../styled'
import GroupModal from './components/GroupModal'

import { formattedDate } from 'utils'
import { useAccountState } from 'state/account/hooks'
import { Group } from 'state/group/actions'
import { useActionHandlers } from 'state/group/hooks'

import { GET_GROUPS } from 'constants/Queries'

interface State {
  open: boolean
  value?: Group
  fetching: boolean
  page: number
  pageSize: number
  selectedItems: { [key: string]: Group }
  selectedAll: boolean
}

export default function AccountGroup() {
  const { t } = useTranslation()
  const { data, refetch } = useQuery(GET_GROUPS)

  const [state, setState] = useState<State>({ 
    open: false,
    value: undefined,
    fetching: false,
    page: 0,
    pageSize: 20,
    selectedItems: {},
    selectedAll: false,
  })

  const { account } = useAccountState()
  const { onAdd, onRemove } = useActionHandlers(account)

  const sortedGroups: Group[] = useMemo(() => {
    return data ? [...data.groups].sort((a: Group, b: Group) => b.createdTime - a.createdTime) : []
  }, [data])

  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl)

  const handleClearAll = (event: React.MouseEvent<HTMLElement>) => {
    Object.values(state.selectedItems).forEach(group => onRemove(group))
    setState({ ...state, selectedItems: {}, selectedAll: false })
  }

  const handleSelected = (group: Group) => (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log({ group, target: event.currentTarget, checked: event.target.checked })
    const { selectedItems } = state
    if (event.target.checked && !selectedItems[group.id]) {
      selectedItems[group.id] = group
    }
    if (!event.target.checked && selectedItems[group.id]) {
      delete selectedItems[group.id]
    }
    
    setState({ 
      ...state, 
      selectedItems,
      selectedAll: sortedGroups.length === Object.keys(selectedItems).length,
    })
  }

  const handleSelectedAll = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.checked) {
      const selectedItems = sortedGroups.reduce((map: { [key: string]: Group }, group) => {
        map[group.id] = group
        return map
      }, {})
      setState({ ...state, selectedItems, selectedAll: true })
    } else {
       setState({ ...state, selectedItems: {}, selectedAll: false })
    }
  }

  const handleClick = (group: Group) => (event: React.MouseEvent<HTMLElement>) => {
    setState({ ...state, value: group })
    setAnchorEl(event.currentTarget)
  }

  const handleClose = () => {
    setAnchorEl(null)
  }

  const handleChange = (group: Group) => {
    console.log({ group })
    onAdd(group)
    refetch()
  }

  const handleEdit = () => {
    setAnchorEl(null)
    state.value && setState({ ...state, open: true })
  }

  const handleRemove = () => {
    setAnchorEl(null)
    state.value && onRemove(state.value)
  }

  return (
    <PageWrapper>
      <Grid container sx={{ p: 2 }}>
        <Grid item xs={8}>
          <Breadcrumbs aria-label="breadcrumb">
            <Link underline="hover" color="inherit" href="#/account">账号</Link>
            <Typography color="text.primary">分组</Typography>
          </Breadcrumbs>
          <Title variant='h1'> {t('分组')}</Title>
        </Grid>
        <Grid item xs={4} sx={{ 
          display: 'flex', 
          justifyContent: 'flex-end', 
          alignItems: 'center',
          gap: '0.5rem'
        }}>
          <Button 
            disableElevation 
            variant="contained" 
            size="large" 
            color="secondary" 
            startIcon={<Plus />}
            sx={{
              borderRadius: '50%',
              width: '48px',
              height: '48px',
              minWidth: '48px',
              '>span': {
                margin: 0,
              }
            }}
            onClick={() => setState({ ...state, open: true, value: undefined })}
          />
          <Button 
            disableElevation 
            variant="contained" 
            size="large" 
            color="secondary" 
            startIcon={<Trash2 />}
            sx={{
              borderRadius: '50%',
              width: '48px',
              height: '48px',
              minWidth: '48px',
              '>span': {
                margin: 0,
              }
            }}
            disabled={Object.keys(state.selectedItems).length === 0}
            onClick={handleClearAll}
          />
        </Grid>
      </Grid>
      <GroupModal 
        open={state.open}
        value={state.value}
        onChange={handleChange}
        onDismiss={() => setState({ ...state, open: false })} 
      />
      <Card sx={{
        backgroundColor: 'rgb(255, 255, 255)',
        color: 'rgba(0, 0, 0, 0.87)',
        transition: 'box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
        overflow: 'hidden',
        borderRadius: '20px',
        margin: '1rem',
        boxShadow: 'rgb(90 114 123 / 11%) 0px 7px 30px 0px'
      }}>
      <CardContent sx={{ padding: '1rem 0' }}>
        <TableContainer component={Paper} sx={{ boxShadow: 'none' }}>
          <Table aria-label="simple table">
            <TableHead>
              <TableRow sx={{
                "& > .MuiTableCell-head": {
                  fontFamily: '"DM Sans", sans-serif',
                  letterSpacing: '0',
                }
              }}>
                  <TableCell sx={{ width: '80px', textAlign: 'center' }}>
                    <Checkbox size="small" checked={state.selectedAll} onChange={handleSelectedAll} />
                  </TableCell>
                  <TableCell>{'名称'}</TableCell>
                  <TableCell sx={{ width: '180px' }}>{'创建时间'}</TableCell>
                  <TableCell sx={{ width: '80px', textAlign: 'right' }}>{''}</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {sortedGroups.map((group, i) => (
                <TableRow
                  key={group.id}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                  >
                  <TableCell sx={{ textAlign: 'center' }}>
                    <Checkbox size="small" checked={!!state.selectedItems[group.id]} onChange={handleSelected(group)} />
                  </TableCell>
                  <TableCell scope="row">
                    {group.name}
                  </TableCell>
                  <TableCell scope="row">
                      {formattedDate(new Date(group.createdTime * 1000))}
                  </TableCell>
                  <TableCell sx={{ textAlign: 'right' }}>
                    <IconButton aria-label="more-actions" onClick={handleClick(group)}>
                      <MoreVertical size={20} />
                    </IconButton>
                    <StyledMenu
                      id="more-action-menu"
                      MenuListProps={{
                        'aria-labelledby': 'more-action-button',
                      }}
                      anchorEl={anchorEl}
                      open={open}
                      onClose={handleClose}
                    >
                      <MenuItem onClick={handleEdit} disableRipple>
                        <Edit size={18} />
                        修改
                      </MenuItem>
                      <MenuItem onClick={handleRemove} disableRipple>
                        <Trash2 size={18} />
                        删除
                      </MenuItem>
                    </StyledMenu>
                  </TableCell>
                </TableRow>
              ))}
              </TableBody>
          </Table>
          </TableContainer>
          </CardContent>
        </Card>
    </PageWrapper>
  )
}