import { createReducer } from '@reduxjs/toolkit'
import {
  Group,
  addGroup,
  removeGroup,
} from './actions'

export interface GroupState {
  [accountId: string]: {
    [id: string]: Group
  }
}

export const initialState: GroupState = {}

export default createReducer(initialState, builder =>
  builder
    .addCase(addGroup, (state, { payload: { accountId, group } }) => {
      const groups = state[accountId] ?? {}
      groups[group.id] = group
      state[accountId] = groups
    })
    .addCase(removeGroup, (state, { payload: { accountId, group } }) => {
      const groups = state[accountId] ?? {}
      if (!groups) return
      delete groups[group.id]
    })
)
