import { useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Account } from 'state/account/actions'
import { useAccountState } from 'state/account/hooks'
import { AppDispatch, AppState } from '../index'
import { CC, addCC, removeCC } from "./actions"

export function useActionHandlers(account?: Account): {
  onAdd: (cc: CC) => void
  onRemove: (cc: CC) => void
} {
  const dispatch = useDispatch<AppDispatch>();

  const onAdd = useCallback(
    (cc: CC) => {
      account && dispatch(addCC({ accountId: account.id, cc }))
    },
    [dispatch, account]
  )

  const onRemove = useCallback(
    (cc: CC) => {
      account && dispatch(removeCC({ accountId: account.id, cc }))
    },
    [dispatch, account]
  )

  return {
    onAdd,
    onRemove,
  };
}

export function useCC(): { [id: string]: CC } {
  const { account } = useAccountState()
  const state =  useSelector<AppState, AppState['cc']>(state => state.cc)

  return account ? state[account.id] ?? {} : {}
}