import React, { useState } from 'react'
import { useTranslation } from 'react-i18next'
import { useMutation } from '@apollo/client'
import {
    styled,
    Box,
    Typography,
    Card,
    CardHeader,
    CardContent,
    CardActions,
    IconButton,
    TextField,
    Autocomplete,
    FormControl,
    OutlinedInput,
    Alert,
    AlertTitle,
 } from '@mui/material'
import LoadingButton from '@mui/lab/LoadingButton'
import { XCircle } from 'react-feather'
import GroupSelect from './GroupSelect'
import CountrySelect from './CountrySelect'

import { Group } from 'state/group/actions'
import { Country } from 'state/cc/actions'
import { Proxy, Task } from 'state/task/actions'
import { DefaultBrowserFingerPrint, ProxyTypes } from 'state/task/hooks'

import { GET_TASKS, CREATE_TASK } from 'constants/Queries'

const ScrollbarWrapper = styled('div')(() => ({
  overflowY: 'auto',
  overflowAnchor: 'none',
  touchAction: 'auto',
  position: 'relative',
  borderRight: '0px !important',
  height: 'calc(100vh - 156px)',
}))

const Label = styled('label')(() => ({
  margin: '1rem 0px 0.5rem !important',
  fontWeight: '500',
  fontSize: '0.875rem !important',
  lineHeight: '1.5',
  fontFamily: '"DM Sans", sans-serif',
  display: 'block',

  ':first-of-type': {
    marginTop: '0 !important',
  }
}))

const StyledOutlinedInput = styled(OutlinedInput)(() => ({
  padding: 0,
  fontSize: '0.875rem',
  '& .MuiOutlinedInput-input': {
    padding: '0.375rem 0.75rem',
  },
  '& .MuiOutlinedInput-notchedOutline': {
      '& > legend': {
          float: 'left !important',
      }
  },
}));

export default function TaskCard({ onChange, onClose }: {
  onChange?: (value: Task) => void
  onClose?: React.MouseEventHandler<HTMLButtonElement>
}) {
  const { t } = useTranslation()

  const [createTask, { data, loading, error }] = useMutation(CREATE_TASK, {
    onCompleted: () => {
      onChange && onChange(data.createTask)
    },
    refetchQueries: [
      {query: GET_TASKS},
      'GetTasks',
    ],
  })

    const [values, setValues] = useState<Task>({
      id: "",
      name: "",
      status: "CLOSE",
      platform: "",
      url: "",
      price: "",
      remark: "",
      proxy: {
        method: "2",
        type: "socks5",
        host: "",
        port: "",
      },
      proxyUrl: undefined,
      browserFingerPrint: DefaultBrowserFingerPrint,
      group: "",
    })

    const handleChange = (prop: keyof Task) => (event: React.ChangeEvent<HTMLInputElement>) => {
      setValues({ ...values, [prop]: event.target.value })
    }

    const handleProxyChange = (prop: keyof Proxy) => (event: React.ChangeEvent<HTMLInputElement>) => {
      const { proxy } = values
      setValues({ ...values, proxy: { ...proxy, [prop]: event.target.value} });
    }

    const handleProxyType = (event: React.SyntheticEvent<Element, Event>, value: { value: string } | null) => {
      const { proxy } = values
      setValues({ ...values, proxy: { ...proxy, type: value ? value.value : ''} })
    }

    const handleGroupChange = (value: Group | null) => {
      setValues({ ...values, group: value?.name ?? ''})
    }

    const handleCountryChange = (value: Country | null) => {
      setValues({ ...values, country: value ?? undefined })
    }

    const handleOK = () => {
      createTask({ variables: { ...values } })
    }
    
    return  (
      <Card elevation={0} sx={{ minWidth: 360 }}>
        <CardHeader title="导入" action={
          <IconButton onClick={onClose} aria-label="task-close">
            <XCircle />
          </IconButton>
        } />
        <CardContent>
          <ScrollbarWrapper>
            <Box sx={{ '& .MuiFormControl-root': { width: '100%' }}}>
              <Typography component={Label}>{t('名称')}</Typography>
              <FormControl variant="outlined">
                <StyledOutlinedInput
                  type="text"
                  placeholder={t('填写任务名称')}
                  value={values.name}
                  onChange={handleChange("name")}
                />
              </FormControl>
              <Typography component={Label}>{t('平台')}</Typography>
              <FormControl variant="outlined">
                <StyledOutlinedInput
                  type="text"
                  placeholder={t('填写平台名称')}
                  value={values.platform}
                  onChange={handleChange("platform")}
                />
              </FormControl>
              <Typography component={Label}>{t('URL')}</Typography>
              <FormControl variant="outlined">
                <StyledOutlinedInput
                  type="text"
                  placeholder={t('填写URL')}
                  value={values.url}
                  onChange={handleChange("url")}
                />
              </FormControl>
              <Typography component={Label}>{t('价格')}</Typography>
              <FormControl variant="outlined">
                <StyledOutlinedInput
                  type="text"
                  placeholder={t('价格（可选）')}
                  value={values.price}
                  onChange={handleChange("price")}
                />
              </FormControl>
              <Typography component={Label}>{t('备注')}</Typography>
              <FormControl variant="outlined">
                <StyledOutlinedInput
                  multiline={true}
                  minRows={2}
                  maxRows={5}
                  placeholder={t('填写任务备注')}
                  value={values.remark}
                  onChange={handleChange("remark")}
                />
              </FormControl>
              <Typography component={Label}>{t('代理类型')}</Typography>
              <FormControl variant="outlined">
                <Autocomplete
                  disablePortal
                  sx={{
                    '.MuiOutlinedInput-root': {
                      padding: 0,
                      fontSize: '0.875rem',
                    },
                    '& .MuiOutlinedInput-notchedOutline': {
                      '& > legend': {
                        float: 'left !important',
                      }
                    },
                  }}
                  options={ProxyTypes}
                  renderInput={(params) => <TextField {...params} />}
                  defaultValue={ProxyTypes[3]}
                  onChange={handleProxyType}
                />
              </FormControl>
              <Typography component={Label}>{t('代理主机')}</Typography>
              <FormControl variant="outlined">
                <StyledOutlinedInput
                  type="text"
                  placeholder={t('代理主机')}
                  value={values.proxy.host}
                  onChange={handleProxyChange("host")}
                />
              </FormControl>
              <Typography component={Label}>{t('代理端口')}</Typography>
              <FormControl variant="outlined">
                <StyledOutlinedInput
                  type="text"
                  placeholder={t('代理端口')}
                  value={values.proxy.port}
                  onChange={handleProxyChange("port")}
                />
              </FormControl>
              <Typography component={Label}>{t('代理账号')}</Typography>
              <FormControl variant="outlined">
                <StyledOutlinedInput
                  type="text"
                  placeholder={t('代理账号（可选）')}
                  value={values.proxy.username}
                  onChange={handleProxyChange("username")}
                />
              </FormControl>
              <Typography component={Label}>{t('代理密码')}</Typography>
              <FormControl variant="outlined">
                <StyledOutlinedInput
                  type="text"
                  placeholder={t('代理密码（可选）')}
                  value={values.proxy.password}
                  onChange={handleProxyChange("password")}
                />
              </FormControl>
              <Typography component={Label}>{t('刷新URL')}</Typography>
              <FormControl variant="outlined">
                <StyledOutlinedInput
                  type="text"
                  placeholder={t('刷新代理的URL（可选）')}
                  value={values.proxyUrl}
                  onChange={handleChange("proxyUrl")}
                />
              </FormControl>
              <Typography component={Label}>{t('分组')}</Typography>
              <FormControl variant="outlined">
                <GroupSelect onChange={handleGroupChange} />
              </FormControl>
              <Typography component={Label}>{t('国家')}</Typography>
              <FormControl variant="outlined">
                <CountrySelect onChange={handleCountryChange} />
              </FormControl>
            </Box>
          </ScrollbarWrapper>
        </CardContent>
        <CardActions sx={{ p: 2, position: 'fixed', bottom: 0 }}>
          {error && (
            <Alert severity="error">
              <AlertTitle>Error</AlertTitle>
              {error.message}
            </Alert>
          )}
          <LoadingButton 
            autoFocus
            disableElevation
            variant="contained" 
            color="secondary"
            loading={loading}
            onClick={handleOK}
          >
            确定
          </LoadingButton>
        </CardActions>
      </Card>
    )
}