import * as React from 'react';
import { useLocation } from 'react-router-dom';
import { styled } from '@mui/material/styles';
import Drawer from '@mui/material/Drawer';
import List from '@mui/material/List';
import { ListSubheader, ListItem, ListItemIcon, ListItemText, Button } from '@mui/material';
import { Box,  Layers, CreditCard, HelpCircle } from 'react-feather'

import { ApplicationModal } from '../../state/application/actions'
import { useModalOpen } from '../../state/application/hooks'

const drawerWidth = 265

const ScrollbarWrapper = styled('div')(() => ({
  overflowY: 'auto',
  overflowAnchor: 'none',
  touchAction: 'auto',
  position: 'relative',
  borderRight: '0px !important',
  height: 'calc(100vh - 5px)',
}))

const Link = styled(Button)(() => ({
  backgroundColor: 'transparent',
  outline: 0,
  border: 0,
  margin: '0px 0px 8px',
  cursor: 'pointer',
  userSelect: 'none',
  verticalAlign: 'middle',
  appearance: 'none',
  color: 'inherit',
  display: 'flex',
  justifyContent: 'flex-start',
  alignItems: 'center',
  position: 'relative',
  textDecoration: 'none',
  width: '100%',
  boxSizing: 'border-box',
  textAlign: 'left',
  padding: '8px 16px',
  transition: 'background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
  borderRadius: '9px',
  // color: 'white',
  // backgroundColor: 'rgb(3, 201, 215) !important',

  '&:hover': {
    backgroundColor: 'rgb(0, 0, 0, 0.03)',
    color: 'inherit',
  },

  '&:active': {
    backgroundColor: 'rgb(3, 201, 215)',
    color: 'white',

    "svg": {
      color: 'white',
    }
  }
}))

const SelectedLink = styled(Button)(() => ({
  backgroundColor: 'rgb(3, 201, 215)',
  outline: 0,
  border: 0,
  margin: '0px 0px 8px',
  userSelect: 'none',
  verticalAlign: 'middle',
  appearance: 'none',
  color: 'white',
  display: 'flex',
  justifyContent: 'flex-start',
  alignItems: 'center',
  position: 'relative',
  textDecoration: 'none',
  width: '100%',
  boxSizing: 'border-box',
  textAlign: 'left',
  padding: '8px 16px',
  transition: 'background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
  borderRadius: '9px',

  "svg": {
      color: 'white',
  },

  '&:hover': {
    backgroundColor: 'rgb(3, 201, 215)',
    color: 'white',
    cursor: 'pointer',
  },
}))

export default function Sidebar() {
  const navOpen = useModalOpen(ApplicationModal.NAV)
  const loaction = useLocation()

  const links = [
    {
      title: '账号',
      subs: [
        {
          name: '任务',
          url: '#/account',
          icon: Box,
        },
        {
          name: '联系人',
          url: '#/account/credit-card',
          icon: CreditCard,
        },
        {
          name: '分组',
          url: '#/account/groups',
          icon: Layers,
        },
      ]
    },
    {
      title: '更多',
      subs: [
        {
          name: '帮助',
          url: '#/help',
          icon: HelpCircle,
        },
      ]
    },
  ]

  const selected = (url: string) => `#${loaction.pathname}` === url

  return (
    <Drawer
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          '& .MuiDrawer-paper': {
            transform: 'none',
            backgroundColor: 'rgb(255, 255, 255)',
            color: 'rgba(0, 0, 0, 0.87)',
            overflowY: 'auto',
            display: 'flex',
            flexDirection: 'column',
            flex: '1 0 auto',
            width: drawerWidth,
            boxSizing: 'border-box',
            zIndex: 1200,
            position: 'fixed',
            top: '100px',
            left: '24px',
            borderRadius: '0.5rem',
            border: '0',
            height: 'calc(100vh - 130px)',
            boxShadow: 'rgb(90 114 123 / 11%) 0px 7px 30px 0px',
            transition: 'transform 225ms cubic-bezier(0, 0, 0.2, 1) 0ms',
          },
        }}
        variant="persistent"
        anchor="left"
        open={navOpen}
      >
        <ScrollbarWrapper>
        <List sx={{ padding: '1rem' }}>
          {links.map(link => (
            <div key={link.title}>
              <ListSubheader sx={{
                fontSize: '0.875rem',
                fontFamily: '"DM Sans", sans-serif',
                fontWeight: '500',
                textTransform: 'uppercase',
                padding: 0,
                margin: '0px 0px 16px',
                lineHeight: '1.57',
                opacity: '0.5',
              }}>
                {link.title}
              </ListSubheader>
              {link.subs.map((item) => (
                <List component="li" key={item.url} sx={{
                  listStyle: 'none',
                  margin: '0',
                  padding: '0',
                  position: 'relative',
                }}>
                  <ListItem component={selected(item.url) ? SelectedLink : Link} href={item.url} key={item.name}>
                    <ListItemIcon sx={{ minWidth: '40px' }}>
                      <item.icon size={20}></item.icon>
                    </ListItemIcon>
                    <ListItemText primary={item.name} />
                  </ListItem>
                </List>
              ))}
            </div>
          ))}
        </List>
        </ScrollbarWrapper>
      </Drawer>
  )
}
