import React, { useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { useQuery, useMutation } from '@apollo/client'
import {
  styled,
  Drawer,
  Box,
  Card,
  CardHeader,
  CardContent,
  CardActions,
  IconButton,
  FormControl,
  Typography,
  Autocomplete,
  TextField,
  Alert,
  AlertTitle,
} from '@mui/material'
import LoadingButton from '@mui/lab/LoadingButton'
import { XCircle } from 'react-feather'
import { useModalOpen, useToggleSettings } from 'state/application/hooks'
import { ApplicationModal } from 'state/application/actions'
import { Label, StyledOutlinedInput } from '../styled'

import { GET_SETTINGS, UPDATE_SETTINGS } from 'constants/Queries'

const ScrollbarWrapper = styled('div')(() => ({
  overflowY: 'auto',
  overflowAnchor: 'none',
  touchAction: 'auto',
  position: 'relative',
  borderRight: '0px !important',
  height: 'calc(100vh - 156px)',
}))

interface Env {
  label: string
  value: string
}

interface State {
  env?: Env
  envConfigs?: string
}

const envs: Env[] = [
  { label: '比特浏览器', value: 'bit' },
]

export default function Settings() {
  const { t } = useTranslation()

  const open = useModalOpen(ApplicationModal.SETTINGS)
  const toggleSettings = useToggleSettings()

  const [values, setValues] = useState<State>({
    env: undefined,
    envConfigs: undefined,
  })

  const { data } = useQuery(GET_SETTINGS)
  const [updateSettings, { loading, error }] = useMutation(UPDATE_SETTINGS, {
    onCompleted: () => {
      toggleSettings()
    },
    refetchQueries: [
      {query: GET_SETTINGS},
      'GetSettings',
    ],
  })

  useEffect(() => {
    if (data) {
      setValues({ ...values, ...data.settings })
    }
  }, [data])

  const handleChange = (prop: keyof State) => (event: React.ChangeEvent<HTMLInputElement>) => {
    setValues({ ...values, [prop]: event.target.value })
  }

  const handleEnvChange = (event: React.SyntheticEvent<Element, Event>, value: Env | null) => {
    setValues({ ...values, env: value ?? undefined })
  }

  const handleOK = () => {
   updateSettings({ variables: { input: { ...values } } })
  }

  return (
    <Drawer
      anchor={'right'}
      sx={{ '.MuiDrawer-paper': { borderRadius: '20px 0 0 20px' }}}
      open={open}
      onClose={toggleSettings}
    >
      <Card elevation={0} sx={{ minWidth: 360 }}>
        <CardHeader title="设置" action={
          <IconButton onClick={toggleSettings} aria-label="settings-close">
            <XCircle />
          </IconButton>
        } />
        <CardContent>
          <ScrollbarWrapper>
            <Box sx={{ '& .MuiFormControl-root': { width: '100%' }}}>
              <Typography component={Label}>{t('环境')}</Typography>
                <Autocomplete
                    disablePortal
                    options={envs}
                    getOptionLabel={(option) => option.label}
                    defaultValue={values.env}
                    renderInput={(params) => <TextField {...params} />}
                    onChange={handleEnvChange}
                    sx={{
                        '.MuiOutlinedInput-root': {
                            padding: 0,
                            fontSize: '0.875rem',
                        },
                        '.MuiOutlinedInput-root .MuiAutocomplete-input': {
                            padding: '0.375rem 0.75rem',
                        },
                        '& .MuiOutlinedInput-notchedOutline': {
                            '& > legend': {
                                float: 'left !important',
                            }
                        },
                    }}
                />
                <Typography component={Label}>{t('环境配置')}</Typography>
                <FormControl variant="outlined">
                  <StyledOutlinedInput
                    type="text"
                    placeholder={t('环境配置')}
                    value={values.envConfigs}
                    onChange={handleChange('envConfigs')}
                  />
                </FormControl>
            </Box>
          </ScrollbarWrapper>
        </CardContent>
        <CardActions sx={{ p: 2, position: 'fixed', bottom: 0 }}>
          {error && (
            <Alert severity="error">
              <AlertTitle>Error</AlertTitle>
              {error.message}
            </Alert>
          )}
          <LoadingButton 
            autoFocus
            disableElevation
            variant="contained" 
            color="secondary"
            loading={loading}
            onClick={handleOK}
          >
            确定
          </LoadingButton>
        </CardActions>
      </Card>
    </Drawer>
  )
}