import { createReducer } from '@reduxjs/toolkit'
import { ApplicationModal, updateSupportedCurrency, setOpenModal } from './actions'

export interface ApplicationState {
  readonly supportedCurrencies: { readonly [currency: string]: number }
  readonly openModal: ApplicationModal | null
}

const initialState: ApplicationState = {
  supportedCurrencies: {},
  openModal: ApplicationModal.NAV
}

export default createReducer(initialState, builder =>
  builder
    .addCase(updateSupportedCurrency, (state, { payload: { currency, betAmount } }) => {
      state.supportedCurrencies[currency] = betAmount
    })
    .addCase(setOpenModal, (state, action) => {
      state.openModal = action.payload
    })
)
